body {
  background-color: white;
}

.center {
  display: grid;
  place-items: center;
  min-height: 100vh;
  margin: 0;
}

.flex {
  display: flex;
  gap: 2rem;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

.kaka {
  width: 200px;
  aspect-ratio: 2/1;
  background-color: none;
}
.kaka:hover {
  width: 400px;
}
.kaka h1 {
  font-size: 30px;
}